import Lottie from 'lottie-react';
import School from '../images/school.png'
import Computer from'../images/computer.json'
import Heart from '../images/heart.json'
// import React, { useState } from 'react';
import '../index.css'
import React, {useState, useRef} from 'react'
import { motion, useScroll, useTransform } from 'framer-motion';
import Resume from '../images/younwooyim_resume.pdf'

const About = (props) => {
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["0 1", "1.33 1"],
  });
  const scaleProgress =  useTransform(scrollYProgress, [0, 1], [0.7, 1])
  const opacityProgress =  useTransform(scrollYProgress, [0, 1], [0.3, 1])

  const icons = ["C++", "C", "C#", "Unity", "SQL", "Flask", "HTML", "CSS", "JavaScript", "React", "Python", "Typescript"]

  return(
    <div className="flex flex-col items-center justify-center min-h-screen text-white bg-black md:p-4">
      <div className="w-full mb-10">
        <h1 className="text-6xl text-center underline font-Dugas">About Me</h1>
      </div>

      <motion.div 
        className="flex flex-row w-full mb-4 text-lg font-Abel"
        ref={ref} 
        style={{
          scale: scaleProgress,
          opacity: opacityProgress,
        }}
      >
        <div className="flex flex-col items-end w-1/2 pl-2 md:pr-2 md:pl-64">
          <ImageComponent src={School} text={"University of Michigan"}/>
          <ImageComponent src={Computer} text={"Bachelor of Computer Science"} />
          <ImageComponent src={Heart} text={"Enthusiastic Learner"}/>
        </div>

        <div className="flex flex-col items-center w-1/2 pr-2 md:pl-2 md:pr-64">
          <div className="w-full h-full p-4 text-sm md:text-xl">
            <p className="flex justify-center mb-4">Hello👋 Thanks for visiting my page. <br /> I am a software engineer recently graudated from University of Michigan-Ann Arbor, and received Bachelor of Computer Science. </p>
            <p className="flex justify-center"> From unraveling the mysteries of algorithms to exploring the latest in tech innovations, my portfolio is more than just a collection of work; it's a reflection of my passion for pushing the boundaries of technology and a commitment to continuous growth in this ever-evolving field.</p>
          </div>
        </div>
      </motion.div>

      {/* Resume Section */}
      <motion.div 
        ref={ref} 
        style={{
          scale: scaleProgress,
          opacity: opacityProgress,
        }}
        className="w-full"
      >
        <a href={Resume} download="Resume_younwooyim"> <h1 className="mb-6 text-2xl font-bold text-center font-Abel hover:underline">Resume</h1></a>
        <div  className="flex justify-center">
          <div className='flex flex-wrap items-center justify-center w-4/5 space-x-2 md:w-1/2 md:space-x-4'>
                {icons.map((icon, index) => (
                    <div key={index} className="rounded-full text-xs md:text-sm bg-white bg-opacity-60 text-black py-0.5 px-4 hover:bg-opacity-80 mb-4">{icon}</div>
                ))}
          </div>
        </div>
      </motion.div>
    </div>
  );
};

const ImageComponent = ({src, text}) => {
  const [isHovered, setIsHovered] = useState(false);
  let is_image = false;
  if (src===School) is_image=true;

  const handleMouseOver = () => {
    setIsHovered(true);
  };

  const handleMouseOut = () => {
    setIsHovered(false);
  };
  

  return (
    <div className="flex items-center justify-end mb-4" onMouseOver={handleMouseOver} onMouseOut={handleMouseOut}> 
      <span className={`animate-slideRight text-white absolute ${isHovered ? 'block' : 'hidden'}`}>{text} </span>
      {is_image? (
        <img
        src={src}
        alt="school logo"
        className={`slide-left ${isHovered ? 'slide-left-active' : ''} mb-2 max-h-20 max-w-20`}
      />
      ):(
        <Lottie animationData={src} className={`slide-left ${isHovered ? 'slide-left-active' : ''} mb-2 max-h-20 max-w-20`}/>
      )}
    </div>
  );
};

export default About;