import React, {useState, useRef} from 'react'
import { motion, useScroll, useTransform} from 'framer-motion';

const Experience = (props) => {
  return (
    <div className="flex flex-col min-h-screen p-4 pt-40 text-white bg-black">
      <div className="w-full mb-10">
        <h1 className="text-6xl text-center underline font-Dugas">Experience</h1>
      </div>

      <EachExperience date={"JUNE - SEPT 2022"} name ={"Hippo tnc"} link={"https://www.hippotnc.com/"} position={"Software Engineer"} description={"My objective is to design and develop virtual reality content that not only assesses intelligence but also provides an engaging and entertaining experience for children, offering a digital alternative to traditional paper-based intelligence tests."} icons={["Unity", "C#", "VR"]}/>
      <EachExperience date={"JUNE - AUG 2023"} name ={"SAMSUNG ELECTRONICS"} link="https://www.samsung.com/us" position={"Software Engineer"} description={"Utilizing user data from air conditioning systems, my task involves refining and analyzing user-specific trends to provide insights for the enhancement of air conditioner features."} icons={["Python", "SQL", "Pandas"]}/>
    </div>
  );
};

const EachExperience = (props) => {
  const { icons = [] } = props;
  
  const [hover, setHover] = useState(false);
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["0 1", "1.33 1"],
  });
  const scaleProgress =  useTransform(scrollYProgress, [0, 1], [0.3, 1])
  const opacityProgress =  useTransform(scrollYProgress, [0, 1], [0.6, 1])

  return (
    <motion.div 
      ref={ref} 
      style={{
        scale: scaleProgress,
        opacity: opacityProgress,
      }}
      className={`flex font-Abel flex-row items-center justify-center mx-12 md:mx-72 mb-5 ${hover ? 'border-2 border-white-500' : ''}`} 
      onMouseEnter={() => setHover(true)} 
      onMouseLeave={() => setHover(false)}
    >
        <div className={`p-4 w-2/5 text-right`}>
          {/* Date component */}
          <p>{props.date}</p>
        </div>
        <div className={`p-4 w-3/5 text-left`}>
          {/* Explanation component */}
          <a href={props.link}><p className='text-xl font-bold hover:underline'>{props.name}</p></a>
          <p className='mb-2 text-opacity-50'>{props.position}</p>
          <p className='mb-2 text-sm md:text-xl'> {props.description} </p>
          <div className='flex items-center justify-start space-x-4'>
            {icons.map((icon, index) => (
                <div key={index} className="rounded-full text-sm bg-white bg-opacity-60 text-black py-0.5 px-4 hover:bg-opacity-80">{icon}</div>
            ))}
          </div>
        </div>
    </motion.div>
  );
}

export default Experience;