import React, {useState, useRef} from 'react'
import { motion, useScroll, useTransform } from 'framer-motion';
import IntelliKare from '../images/intellikare.png'
import ImageColorization from '../images/imagecolorization.jpeg'

const Projects = (props) => {
  return (
    <div className="flex flex-col min-h-screen p-4 pt-40 text-white bg-black">
      <div className="w-full mb-10">
        <h1 className="text-6xl text-center underline font-Dugas">Projects</h1>
      </div>

      <EachProject src={IntelliKare} link={"https://github.com/younw00/IntelliKare.git"} name={"IntelliKare"} icons={["Unity", "C#", "VR"]} text={"This project is a collaborative effort focused on the development of a Virtual Reality (VR) application for administering the K-WISC-V intelligence test."}/>
      <EachProject src={ImageColorization} link={"https://github.com/younw00/Image-Colorzation.git"} name={"Image Colorization"} icons={["python", "PyTorch"]} text={"This is a collaborative team project centered on employing advanced computer vision techniques to effectively colorize grayscale images"}/>
    </div>
  );
};

const EachProject = (props) => {
  const { icons = [] } = props;
  
  const [hover, setHover] = useState(false);
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["0 1", "1.33 1"],
  });
  const scaleProgress =  useTransform(scrollYProgress, [0, 1], [0.3, 1])
  const opacityProgress =  useTransform(scrollYProgress, [0, 1], [0, 1])

  return (
    <a href={props.link}>
      <motion.div 
        ref={ref} 
        style={{
          scale: scaleProgress,
          opacity: opacityProgress,
        }}
        className={`flex flex-row items-center justify-start mx-10 md:mx-72 mb-5 font-Abel text-xl ${hover ? 'border-2 border-white-500' : ''}`} 
        onMouseEnter={() => setHover(true)} 
        onMouseLeave={() => setHover(false)}
      > 
          <div className={`p-2 md:p-4 w-1/2`}>
            {/* Image component */}
            <img src={props.src} alt="Project Image" />
          </div>
          <div className={`p-2 md:p-4 w-1/2`}>
            {/* Explanation component */}
            <p className='mb-2 text-xl font-bold'>{props.name}</p>
            <p className='mb-2 text-sm md:text-xl'>{props.text}</p>
            <div className='flex items-center justify-start space-x-4'>
              {icons.map((icon, index) => (
                  <div key={index} className="rounded-full text-sm bg-white bg-opacity-60 text-black py-0.5 px-4 hover:bg-opacity-80">{icon}</div>
              ))}
            </div>
          </div>
        </motion.div>
    </a>
  );
}

export default Projects;