const AnimateText = ({text}) => {
  
  const letters = text.split('');
  let condition = "flex content-end space-x-2 md:space-x-6 ml-2 md:ml-64"
  let index = 0;
  if (text === "YIM") {
    condition = "flex content-end space-x-2 md:space-x-6 justify-end mr-2 md:mr-64";
    index = 8;
  }
  
  return (
    <h1 className={condition}>
      {letters.map((letter) => (
        <span
          key={index++}
          style={{ animationDelay: `${index * 0.1}s`, opacity: 0}}
          className={`animate-enlargeDownward text-white font-Dugas text-9xl lg:text-15xl leading-none`}
        >
          {letter}
        </span>
      ))}
    </h1>
  );

};

export default AnimateText;