import React, { useState } from 'react';
import axios from 'axios';

const Contact = (props) => {
  return (
    <div className="flex flex-col min-h-screen p-4 pt-40 text-white bg-black">
      <div className="w-full mb-10">
        <h1 className="text-6xl text-center underline font-Dugas">Contact</h1>
      </div>
    
      <ContactForm />
    </div>
  );
};

const ContactForm = () => {
  const [formState, setFormState] = useState({
    name: '',
    email: '',
    message: '',
  });

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await axios.post('https://portfolio-end-034b41024896.herokuapp.com/submit-form', {
        ...formState
      })
      setFormState({
        name: '',
        email: '',
        message: '',
      });
      alert("Sent successfully!");
        
    } catch (error) {
        console.error('Error submitting form:', error);
        // Handle error (show an error message)
    }
  };

  const handleChange = (e) => {
    setFormState({
      ...formState,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-md mx-auto font-Abel" action='POST'>
      <div className="mb-6">
        <label htmlFor="name" className="block mb-2 text-lg font-medium text-center text-white">Name</label>
        <input
          type="text"
          id="name"
          name="name"
          value={formState.name}
          onChange={handleChange}
          required
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
        />
      </div>
      <div className="mb-6">
        <label htmlFor="email" className="block mb-2 text-lg font-medium text-center text-white">Email</label>
        <input
          type="email"
          id="email"
          name="email"
          value={formState.email}
          onChange={handleChange}
          required
          className="bg-gray-50 border text-black border-gray-300 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
        />
      </div>
      <div className="mb-6">
        <label htmlFor="message" className="block mb-2 text-lg font-medium text-center text-white">Message</label>
        <textarea
          id="message"
          name="message"
          value={formState.message}
          onChange={handleChange}
          required
          className="bg-gray-50 border text-black border-gray-300 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
          rows="4"
        ></textarea>
      </div>
      <div className='flex items-center justify-center'>
        <button
          type="submit"
          className=" text-white bg-black-500 border-white hover:bg-white hover:text-black focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center"
        >
          Send
        </button>
      </div>
    </form>
  );
};

export default Contact;